.flash {
    background-color: #dbedff;
    border: 1px solid rgba(27, 31, 35, .15);
    border-radius: 3px;
    color: #032f62;
    position: relative;
    padding-left: 16px;
    height: 50px;
    transition: height .25s ease;
    overflow-y: hidden;
    display: flex;
    &.h0 {
        height: 0 !important;
        border: none !important; }
    &-close {
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        background: none;
        border: 0;
        color: inherit;
        cursor: pointer;
        opacity: .6;
        padding: 16px;
        text-align: center;
        order: 1;
        &:hover {
            opacity: 1; }
        &:focus {
            outline: none; } }
    &-warn {
        background-color: #fffbdd;
        color: #735c0f; }
    &-error,
    &-warn {
        border-color: rgba(27, 31, 35, .15); }
    &-error {
        background-color: #ffdce0;
        color: #86181d; }
    &-success {
        background-color: #dcffe4;
        border-color: rgba(27, 31, 35, .15);
        color: #165c26; }
    &-full {
        border-radius: 0;
        border-width: 1px 0; }
    p:last-child {
        margin-bottom: 0; }
    .message {
        order: 0;
        flex-grow: 1;
        align-self: center;
        height: inherit;
        display: flex;
        align-items: center;
        overflow: hidden; } }


